import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {BrowserRouter as Router, Route} from "react-router-dom";

ReactDOM.render(
    <React.StrictMode>
        <Router basename={process.env.PUBLIC_URL}>
            <Route path="/">
                <App/>
            </Route>
        </Router>
    </React.StrictMode>,
    document.getElementById('root')
);
